










import { getCustomerService } from '@/request/API/index.js';
export default {
	data() {
		return {
			servicePhone:''
		};
	},
	onShow() {
		this.getCustomerService();
	},
	methods: {
		getCustomerService() {
			getCustomerService().then(res => {
				if(res.data.code==0){
					this.servicePhone=res.data.data[0].servicePhone
				}
			});
		},
		callUp() {
			uni.makePhoneCall({
				phoneNumber:this.servicePhone
			});
		}
	}
};
